'use client';

import { useState, useEffect } from 'react';
import TaskList from './TaskList';
import SanctuaryToggle from './SanctuaryToggle';
import FlarePanel from './FlarePanel';
import styles from './Dashboard.module.css';

export default function Dashboard() {
  const [tasks, setTasks] = useState([]);
  const [flares, setFlares] = useState([]);
  const [sanctuary, setSanctuary] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Fetch tasks from API
  useEffect(() => {
    const fetchTasks = async () => {
      try {
        setLoading(true);
        const res = await fetch('/api/tasks');
        if (!res.ok) throw new Error('Failed to fetch tasks');
        const data = await res.json();
        setTasks(data.tasks || []);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchTasks();
  }, []);

  // Toggle Sanctuary Mode
  useEffect(() => {
    const viewport = document.querySelector('.main-viewport');
    if (viewport) {
      if (sanctuary) {
        viewport.classList.add('sanctuary-active');
      } else {
        viewport.classList.remove('sanctuary-active');
      }
    }
  }, [sanctuary]);

  const handleFlare = async (flareData) => {
    try {
      const res = await fetch('/api/flare', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(flareData),
      });
      if (!res.ok) throw new Error('Failed to send flare');
      const newFlare = await res.json();
      setFlares([newFlare, ...flares]);
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <div className={`${styles.viewport} main-viewport`}>
      <div className={`${styles.contentLayer} content-layer`}>
        <header className={styles.header}>
          <h1>üéØ Slack for School HUD</h1>
          <p>Your focus-first task manager</p>
        </header>

        <main className={styles.main}>
          {error && <div className={styles.error}>Error: {error}</div>}
          {loading ? (
            <p className={styles.loading}>Loading tasks...</p>
          ) : (
            <TaskList tasks={tasks} />
          )}
        </main>
      </div>

      {/* HUD Layer (fixed, always visible) */}
      <aside className={`${styles.hudLayer} hud-layer`}>
        <SanctuaryToggle active={sanctuary} onChange={setSanctuary} />
        <FlarePanel onFlare={handleFlare} recentFlares={flares.slice(0, 3)} />
      </aside>
    </div>
  );
}
